.about-us-page {
    max-width: 1200px;
    margin: 0 auto;
    padding: 60px 20px;
}

.image-container {
    display: flex;
}

.about-us-image {
    height: 200px;
}

.about-us-page h1 {
    margin-bottom: 30px;
}

.about-us-page p {
    margin-bottom: 20px;
    line-height: 1.5;
}

.about-us-page ul {
    list-style: disc;
    margin: 0 0 24px 48px;
}

.about-us-page li {
    line-height: 1.5;
}

.justified-content {
    text-align: justify;
}

