.contact-us-page {
    max-width: 1200px;
    margin: 0 auto;
    padding: 60px 20px;
}

.contact-us-page h1 {
    margin-bottom: 30px;
}

.contact-details-container {
    display: grid;
    gap: 1.5rem;
    justify-content: center;
    /* Set default number of columns */
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
}

.contact-us-page .card {
    border: 1px solid #e3e3e3;
    min-height: 150px;
    position: relative;
    border-radius: 5px;
    -moz-border-radius: 5px;
    -webkit-border-radius: 5px;
    margin-bottom: 20px;
    overflow: hidden;
    padding: 0 20px 0 170px;
    white-space: nowrap;
}

.contact-us-page .card-info {
    display: inline-block;
    vertical-align: middle;
    padding: 20px 0;
    white-space: normal;
    line-height: 1.5;
}

.contact-us-page h4 {
    margin-bottom: 10px;
}

.contact-us-page .fa {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    width: 130px;
    background-color: #000000;
    text-align: center;
    font-size: 50px;
    color: #FFF;
    line-height: 150px;
    transition: background-color ease-in 200ms;
    -moz-transition: background-color ease-in 200ms;
    -webkit-transition: background-color ease-in 200ms;
    -o-transition: background-color ease-in 200ms;
    -ms-transition: background-color ease-in 200ms;
}

.fa-map-marker:before {
    content: "\f041";
}

.fa-envelope:before {
    content: "\f0e0";
}

.fa-phone:before {
    content: "\f095";
}

.fa-clock-o:before {
    content: "\f017";
}
