.about-us-section {
    text-align: center;
    background: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%);
    color: white;
    padding: 64px;
}

.about-us-text {
    margin: 50px 120px 45px;
    line-height: 1.5;
    text-align: justify;
}

.about-us-btn {
    display: inline-block;
    padding: 10px 43px;
    color: #fff !important;
    position: relative;
    transition: all 300ms ease-in-out 0s;
    background: #8a8989;
    margin: 0 0 20px 0;
    text-decoration: none;
}

